import React, { lazy, Suspense }  from 'react'
//import  Audio_Tool from './Audio_Tool';
const Audio_Tool = lazy(() => import('./Audio_Tool'));
const renderLoader = () => <p style={{display: "none"}}>Loading</p>;

class AudioWorkSpace extends React.Component {
    constructor(props){
        super(props);
        this.state = { }
    }

    render() {

      return (
          <div>
              <div className="Audio-WS-Wrap" >
                <Suspense fallback={renderLoader()}>
                  <Audio_Tool/>
                </Suspense>
              </div>
          </div>
      );
    }
  }

export default AudioWorkSpace
